import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      component: () => import('@/pages/index/index')
    },
    {
      path: '/culture/venue/list',
      name: 'VenueList',
      component: () => import('@/pages/culture/venue/list/index')
    },
    {
      path: '/culture/venue/detail',
      name: 'VenueDetail',
      component: () => import('@/pages/culture/venue/detail/index')
    },
    {
      path: '/culture/venue/order',
      name: 'VenueOrder',
      component: () => import('@/pages/culture/venue/order/index')
    },
    {
      path: '/culture/activity/list',
      name: 'ActivityList',
      component: () => import('@/pages/culture/activity/list/index')
    },
    {
      path: '/culture/activity/detail',
      name: 'ActivityDetail',
      component: () => import('@/pages/culture/activity/detail/index')
    },
    {
      path: '/culture/activity/order',
      name: 'ActivityOrder',
      component: () => import('@/pages/culture/activity/order/index')
    }
  ]
})



