import Vue from 'vue'
import Vuex from 'vuex'
import 'es6-promise/auto'
Vue.use(Vuex)
const store = new Vuex.Store({
    state: {
        //返回路径
        backpath: "",
    },
    mutations: {
        setBackpath(state,path){
            state.backpath=path;
        }
    },
    getters: {
        getBackpath(state) {
            return state.backpath;
        }
    },
    actions: {
       
    }

})
export default store


